import React from 'react'

import NavBar from '../components/NavBar.jsx'
import LinkedinIcon from '../images/linkedin-icon.svg'
import '../components/contactPage.css'

const ContactPage = props => {
  return (
    <div className="contact-page">
      <NavBar />
      <div className="contact-page-contents">
        <h1>Contact Me</h1>
        <p>
          For all enquires please use my personal email{' '}
          <strong>Troy_budd95@msn.com</strong>
        </p>
        <p className="contact-page-contents__connect">
          Or connect with me
          <br />
          <a
            href="https://www.linkedin.com/in/troybudd/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              className="contact-user__icon"
              src={LinkedinIcon}
              alt={'Linkedin brand logo'}
            />
          </a>
        </p>
      </div>
    </div>
  )
}

export default ContactPage
